.app {
	height: 500px;
}
html,
body {
	position: relative;
	height: 100%;
}

body {
	background: #eee;
	font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
	font-size: 14px;
	color: #000;
	margin: 0;
	padding: 0;
}

.swiper {
	width: 100%;
	padding-top: 50px;
	padding-bottom: 50px;
}

.swiper-slide {
	background-position: center;
	background-size: cover;
	width: 400px;
	background-color: aqua;
	margin-bottom: 30px;
}

.swiper-slide img {
	display: block;
	width: 100%;
}

.swiper-3d .swiper-slide-shadow-left {
	background-image: none;
}
.swiper-3d .swiper-slide-shadow-right {
	background-image: none;
}
